<template>
  <div id="app">
    <UpLoad />
    <el-table :data="this.$store.state.menu" style="width: 100%">
      <el-table-column prop="index" label="序号"> </el-table-column>
      <el-table-column
        prop="filename"
        label="文件名"
        min-width="120"
        :show-overflow-tooltip="true"
      >
      </el-table-column>
      <el-table-column label="下载">
        <template slot-scope="scope">
          <a
            :download="scope.row.filename"
            :href="'./public/files/' + scope.row.filename"
            >下载</a
          >
        </template>
      </el-table-column>
      <el-table-column label="预览">
        <template slot-scope="scope">
          <el-button
            type="primary"
            size="default"
            @click="
              preview(scope.row.filename, '/public/files/' + scope.row.filename)
            "
            >预览</el-button
          >
        </template>
      </el-table-column>
      <el-table-column label="删除">
        <template slot-scope="scope">
          <el-button
            type="danger"
            size="default"
            @click="open(scope.row.filename)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div>
      <el-dialog
        title="预览窗口"
        :visible.sync="centerDialogVisible"
        width="60%"
        center
      >
        <div>
          <img
            :src="url"
            alt=""
            v-if="this.pics.includes(this.type)"
            class="perv"
          />
          <audio
            :src="url"
            v-else-if="this.audios.includes(this.type)"
            controls
            class="perv"
          ></audio>
          <video
            :src="url"
            v-else-if="this.videos.includes(this.type)"
            controls
            class="perv"
          ></video>
          <span v-else class="perv">浏览器不支持此类型文件!</span>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script lang="js">
import { DeleteF,getInfo } from "@/apis";
import UpLoad from "@/components/UpLoad.vue";
export default {
  name: "App",
  components: { UpLoad },
  data() {
    return {
      url:"",
      centerDialogVisible:false,
      type:"",
      pics:['jpg', 'png', 'webp', 'jpeg', 'bmp', 'gif'],
      audios:['mp3', 'ogg'],
      videos:['flv', 'swf', 'mp4', 'rmvb', '3gp', 'mpeg', 'wmv', 'avi', 'mov', 'mpv']
    };
  },
  methods: {
    getMenu() {
      getInfo().then((res)=>{
        this.$store.dispatch("updata",res.data)
      })
    },

    preview(filename,value){
      let mat=filename.match(/\..*$/g)[0]
      this.type=mat.replace(".","")
      this.url=value
      this.centerDialogVisible=true
    },
    open(filename) {
      this.$confirm(`此操作将永久删除${filename}, 是否继续?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          DeleteF(filename)
            .then((res) => {
              // console.log(res)
              this.$message({
                type: "success",
                message: `${res.data}`,
              });
            })
            .catch((error) => {
              this.$message({
                type: "warning",
                message: `${error}`,
              });
            });
          this.getMenu();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  mounted() {
    this.getMenu();
  },
};
</script>

<style scoped>
#app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

a {
  text-decoration: none;
  color: #409eff;
}

.perv {
  max-width: 100%;
}
</style>
